#drum-machine {
    width: 450px; 
}

#drum-machine-header {
    height: 50px; 
    background-color: var(--drum-machine-color); 
    border: 1px solid var(--green-2); 
    display: flex; 
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

#drum-machine-header-left {
    display: flex; 
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-left: 10px; 
}

#actual-song {
    font-size: 25px; 
    color: var(--white); 
}

#play-complete {
    background-color: var(--green-1);
    color: var(--drum-machine-color);
    margin-left: 10px;
    width: 25px;
    height: 25px; 
    border-radius: 25%; 
    display: flex; 
    justify-content: center;
    align-items: center;
}

#songs-dropdown {
    margin-right: 20px; 
    width: 20px;
    height: 20px;  
    font-size: 1rem; 
    color: var(--drum-machine-color);
    border: 1px solid var(--green-1);
    background-color: var(--green-2);
    cursor: pointer; 
}

#drum-machine-content {
    height: 400px;
    background-color: var(--drum-machine-color); 
    display: grid; 
    grid-template-rows: 1fr 5fr;
    border-bottom: 1px solid var(--white); 
    border-left: 1px solid var(--white); 
    border-right: 1px solid var(--white); 
}

#drum-machine-content-top{
    grid-column: 1 / 2; 
    display: grid; 
    grid-template-columns: repeat(3, 1fr);
}

#power-button{
    margin: auto; 
    border-radius: 5px; 
    width: 75px; 
    height: 30px; 
}

.turned-on {
    background-color: var(--green-1);
}

.turned-off {
    background-color: var(--gray); 
}

#display {
    background-color: white;
    margin: auto;  
    height: 80%; 
    width: 80%; 
    border-radius: 5px;
    display: flex; 
    justify-content: center;
    align-items: center;
}



#volume-slider { 
    -webkit-appearance: none;
    margin: auto; 
    width: 75px;
    height: 10px;
    border-radius: 5px;
    background: #E8E2E2;
    background-image: linear-gradient(#1db954, #1ed760);
    background-size: 50% 100%;
    background-repeat: no-repeat;
    cursor: pointer;
}

#pad-container {
    display: flex; 
    flex-direction: row; 
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 10px 5px 20px 5px; 
    column-gap: 2px; 
    row-gap: 1px; 
}

.drum-pad {
    font-size: 1.5rem;
    font-weight: bold;
    border-radius: 5px;
    width: 24%; 
    height: 32.5%;  
    transition-property: font-size, color; 
    transition-duration: 0.2s;
    transition-timing-function: ease-in-out;
}

.drum-pad:active, .drum-pad-clicked {
    font-size: 2rem; 
    color: var(--white);
}
