:root {
  --background-color-1: #03001C;
  --background-color-2: #301E67;
  --drum-machine-color: #151515;  
  --green-1: #1ed760; 
  --green-2: #1db954; 
  --white: #E8E2E2; 
  --gray: #DDDDDD; 
}

* {
  margin: 0;
  box-sizing: border-box;
  font-family: "Montserrat", 'Gill Sans', 'Gill Sans MT';
}

button {
  border: none; 
  cursor: pointer; 
}

.App {
  height: 100vh; 
  min-height: 530px;
  text-align: center;
  display: flex; 
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding-bottom: 30px; 
}

#my-name {
  background-color: var(--white);
  padding: 3px 5px; 
  margin-top: 10px; 
}

#my-name a {
  text-decoration: none; 
  color: var(--background-color-2);
  transition-property: font-weight;
  transition-duration: 0.5s;
}

#my-name a:hover {
  font-weight: bold; 
}

#my-name a:active {
  color: var(--background-color-1);
}

.bg {
  background-image: linear-gradient(-60deg, var(--background-color-2) 55%, var(--background-color-1) 45%);
  bottom:0;
  left:-50%;
  position:fixed;
  right:-50%;
  top:0;
  z-index:-1;
  }
